body {
 background-color: #C8D0D2;
 margin: 0;
}

.container {
    max-width: 900px;
    margin: 1rem 2rem;
}

.card-grid {
   display: grid;
   align-items: center;
   grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
   gap: 1rem;
}

.card {
    display: flex; 
    justify-content: center;
    align-items: center;
    position: relative;
    border-radius: .25rem;
    background-color: white;
    box-shadow: 0 0 5px 2px rgba(0, 0, 0 , 0.3);
    transform: perspective(1000px) rotateY(var(--rotate-y, 0)) translateY(var(--translate-y, 0));
    transform-style: preserve-3d;
    transition: 250ms;
}

.card.flip {
    --rotate-y: 180deg;
}

.card .front{
    left: 0;
}

.card .front,
.card .back {
    position: absolute;
    padding: 1rem;
    backface-visibility: hidden;
}

.card .back {
    transform: rotateY(180deg);
}

.card:hover {
    box-shadow: 0 0 5px 2px rgba(0, 0, 0 , 0.6);
    --translate-y: -2px;
}

.options {
    margin-top: .5rem;
}

.flashcard-option {
    margin-top: .25rem;
    color: #555;
    font-size: .75rem;
    cursor: pointer;
}

.flashcard-option:first-child {
    margin-top: 0;
}

.header {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    background-color: white;
    box-shadow: 0 0 5px 2px rgba(0, 0, 0 , 0.3);
    padding: 0 1.5rem;
    top: 0;
}

.form-group {
    margin: .5rem;
    display: flex;
    flex-direction: column;
}

.form-group > label {
    color: #777;
    font-size: .75rem;
    margin-bottom: .25rem;
}

.button {
    background-color: hsl(200, 100%, 50%);
    color: white;
    padding: .5em 1em;
    border-radius: .3em;
    border: none;
    cursor: pointer;
    transition: .3s ease;
}

.button:hover {
    background-color: hsl(200, 100%, 40%);
    color: rgb(240, 236, 236);
}
